<template>
  <div class="rules-container">
    <div class="page-header">
      <h1></h1>
      <div>
        <el-button type="primary" @click="$router.push('/servers')"></el-button>
        <el-button type="success" @click="deployIptato" :loading="deploying"></el-button>
      </div>
    </div>

    <div v-if="server" class="server-info">
      <h2>{{ server.name }} <el-tag :type="server.status === 'online' ? 'success' : 'danger'">{{ server.status === 'online' ? '' : '' }}</el-tag></h2>
      <p>{{ server.host }}:{{ server.port }} ({{ server.username }})</p>
    </div>

    <el-tabs v-model="activeTab" type="card">
      <el-tab-pane label="" name="outbound">
        <el-card>
          <div slot="header">
            <span></span>
          </div>
          <el-button-group>
            <el-button type="primary" @click="blockBTPT" :loading="loading">BT/PT</el-button>
            <el-button type="primary" @click="blockSPAM" :loading="loading">SPAM</el-button>
            <el-button type="primary" @click="blockAll" :loading="loading"></el-button>
          </el-button-group>
          
          <el-divider></el-divider>
          
          <el-form :inline="true" @submit.native.prevent="blockCustomPorts">
            <el-form-item label="">
              <el-input v-model="customPorts" placeholder=": 6881,6882-6889"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="warning" @click="blockCustomPorts" :loading="loading"></el-button>
            </el-form-item>
          </el-form>
          
          <el-form :inline="true" @submit.native.prevent="blockCustomKeyword">
            <el-form-item label="">
              <el-input v-model="customKeyword" placeholder=": youtube.com"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="warning" @click="blockCustomKeyword" :loading="loading"></el-button>
            </el-form-item>
          </el-form>
        </el-card>

        <el-card style="margin-top: 20px;">
          <div slot="header">
            <span></span>
          </div>
          <el-button-group>
            <el-button type="success" @click="unblockBTPT" :loading="loading">BT/PT</el-button>
            <el-button type="success" @click="unblockSPAM" :loading="loading">SPAM</el-button>
            <el-button type="success" @click="unblockAll" :loading="loading"></el-button>
          </el-button-group>
          
          <el-divider></el-divider>
          
          <el-form :inline="true" @submit.native.prevent="unblockCustomPorts">
            <el-form-item label="">
              <el-input v-model="customUnblockPorts" placeholder=": 6881,6882-6889"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="success" @click="unblockCustomPorts" :loading="loading"></el-button>
            </el-form-item>
          </el-form>
          
          <el-form :inline="true" @submit.native.prevent="unblockCustomKeyword">
            <el-form-item label="">
              <el-input v-model="customUnblockKeyword" placeholder=": youtube.com"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="success" @click="unblockCustomKeyword" :loading="loading"></el-button>
            </el-form-item>
          </el-form>
          
          <el-button type="success" @click="unblockAllKeywords" :loading="loading"></el-button>
        </el-card>
      </el-tab-pane>

      <el-tab-pane label="" name="inbound">
        <el-card>
          <div slot="header">
            <span></span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="refreshInboundPorts"></el-button>
          </div>
          
          <el-table v-loading="loadingPorts" :data="inboundPorts" style="width: 100%">
            <el-table-column prop="port" label="" width="180"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="danger" size="mini" @click="disallowPort(scope.row.port)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          
          <el-divider></el-divider>
          
          <el-form :inline="true" @submit.native.prevent="allowPort">
            <el-form-item label="">
              <el-input v-model="portToAllow" placeholder=": 80,443"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="allowPort" :loading="loading"></el-button>
            </el-form-item>
          </el-form>
        </el-card>

        <el-card style="margin-top: 20px;">
          <div slot="header">
            <span>IP</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="refreshInboundIPs"></el-button>
          </div>
          
          <el-table v-loading="loadingIPs" :data="inboundIPs" style="width: 100%">
            <el-table-column prop="ip" label="IP" width="180"></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="danger" size="mini" @click="disallowIP(scope.row.ip)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          
          <el-divider></el-divider>
          
          <el-form :inline="true" @submit.native.prevent="allowIP">
            <el-form-item label="IP">
              <el-input v-model="ipToAllow" placeholder=": 192.168.1.1"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="allowIP" :loading="loading"></el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </el-tab-pane>

      <el-tab-pane label="" name="status">
        <el-card>
          <div slot="header">
            <span></span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="refreshBlockList"></el-button>
          </div>
          
          <pre v-if="blockList" class="output">{{ blockList }}</pre>
          <div v-else>...</div>
        </el-card>

        <el-card style="margin-top: 20px;">
          <div slot="header">
            <span>SSH</span>
            <el-button style="float: right; padding: 3px 0" type="text" @click="refreshSSHPort"></el-button>
          </div>
          
          <pre v-if="sshPortStatus" class="output">{{ sshPortStatus }}</pre>
          <div v-else>...</div>
        </el-card>

        <el-card style="margin-top: 20px;">
          <div slot="header">
            <span></span>
          </div>
          
          <el-button type="danger" @click="confirmClearRules"></el-button>
        </el-card>
      </el-tab-pane>
    </el-tabs>

    <el-card v-if="commandOutput" style="margin-top: 20px">
      <div slot="header">
        <span></span>
      </div>
      <pre class="output">{{ commandOutput }}</pre>
    </el-card>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'RulesView',
  props: {
    serverId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      activeTab: 'outbound',
      loading: false,
      deploying: false,
      loadingPorts: false,
      loadingIPs: false,
      server: null,
      blockList: '',
      sshPortStatus: '',
      inboundPorts: [],
      inboundIPs: [],
      commandOutput: '',
      customPorts: '',
      customKeyword: '',
      customUnblockPorts: '',
      customUnblockKeyword: '',
      portToAllow: '',
      ipToAllow: ''
    };
  },
  computed: {
    ...mapGetters('servers', ['getLoading'])
  },
  created() {
    this.loadServerInfo();
    this.refreshBlockList();
    this.refreshSSHPort();
    this.refreshInboundPorts();
    this.refreshInboundIPs();
  },
  methods: {
    ...mapActions('servers', [
      'getServer',
      'deployIptato'
    ]),
    ...mapActions('rules', [
      'getBlockList',
      'blockBTPTAction',
      'blockSPAMAction',
      'blockAllAction',
      'blockCustomPortsAction',
      'blockCustomKeywordAction',
      'unblockBTPTAction',
      'unblockSPAMAction',
      'unblockAllAction',
      'unblockCustomPortsAction',
      'unblockCustomKeywordAction',
      'unblockAllKeywordsAction',
      'getInboundPorts',
      'getInboundIPs',
      'allowInboundPortsAction',
      'disallowInboundPortsAction',
      'allowInboundIPsAction',
      'disallowInboundIPsAction',
      'getSSHPort',
      'clearAllRulesAction'
    ]),
    async loadServerInfo() {
      try {
        const response = await this.getServer(this.serverId);
        this.server = response.data;
      } catch (error) {
        this.$message.error(': ' + error.message);
      }
    },
    async refreshBlockList() {
      try {
        this.loading = true;
        const response = await this.getBlockList(this.serverId);
        this.blockList = response.data || '';
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async refreshSSHPort() {
      try {
        this.loading = true;
        const response = await this.getSSHPort(this.serverId);
        this.sshPortStatus = response.data || 'SSH';
      } catch (error) {
        this.$message.error('SSH: ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async refreshInboundPorts() {
      try {
        this.loadingPorts = true;
        const response = await this.getInboundPorts(this.serverId);
        // : 
        const portsText = response.data || '';
        const portMatches = portsText.match(/\b\d{1,5}\b/g) || [];
        this.inboundPorts = portMatches.map(port => ({ port }));
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loadingPorts = false;
      }
    },
    async refreshInboundIPs() {
      try {
        this.loadingIPs = true;
        const response = await this.getInboundIPs(this.serverId);
        // : IP
        const ipsText = response.data || '';
        const ipMatches = ipsText.match(/\b(?:\d{1,3}\.){3}\d{1,3}\b/g) || [];
        this.inboundIPs = ipMatches.map(ip => ({ ip }));
      } catch (error) {
        this.$message.error('IP: ' + error.message);
      } finally {
        this.loadingIPs = false;
      }
    },
    async deployIptato() {
      try {
        this.deploying = true;
        await this.deployIptato(this.serverId);
        this.$message.success('');
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.deploying = false;
      }
    },
    async blockBTPT() {
      try {
        this.loading = true;
        const response = await this.blockBTPTAction(this.serverId);
        this.commandOutput = response.data;
        this.$message.success('BT/PT');
        this.refreshBlockList();
      } catch (error) {
        this.$message.error('BT/PT: ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async blockSPAM() {
      try {
        this.loading = true;
        const response = await this.blockSPAMAction(this.serverId);
        this.commandOutput = response.data;
        this.$message.success('SPAM');
        this.refreshBlockList();
      } catch (error) {
        this.$message.error('SPAM: ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async blockAll() {
      try {
        this.loading = true;
        const response = await this.blockAllAction(this.serverId);
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshBlockList();
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async blockCustomPorts() {
      if (!this.customPorts) {
        this.$message.warning('');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.blockCustomPortsAction({
          serverId: this.serverId,
          ports: this.customPorts
        });
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshBlockList();
        this.customPorts = '';
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async blockCustomKeyword() {
      if (!this.customKeyword) {
        this.$message.warning('');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.blockCustomKeywordAction({
          serverId: this.serverId,
          keyword: this.customKeyword
        });
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshBlockList();
        this.customKeyword = '';
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async unblockBTPT() {
      try {
        this.loading = true;
        const response = await this.unblockBTPTAction(this.serverId);
        this.commandOutput = response.data;
        this.$message.success('BT/PT');
        this.refreshBlockList();
      } catch (error) {
        this.$message.error('BT/PT: ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async unblockSPAM() {
      try {
        this.loading = true;
        const response = await this.unblockSPAMAction(this.serverId);
        this.commandOutput = response.data;
        this.$message.success('SPAM');
        this.refreshBlockList();
      } catch (error) {
        this.$message.error('SPAM: ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async unblockAll() {
      try {
        this.loading = true;
        const response = await this.unblockAllAction(this.serverId);
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshBlockList();
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async unblockCustomPorts() {
      if (!this.customUnblockPorts) {
        this.$message.warning('');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.unblockCustomPortsAction({
          serverId: this.serverId,
          ports: this.customUnblockPorts
        });
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshBlockList();
        this.customUnblockPorts = '';
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async unblockCustomKeyword() {
      if (!this.customUnblockKeyword) {
        this.$message.warning('');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.unblockCustomKeywordAction({
          serverId: this.serverId,
          keyword: this.customUnblockKeyword
        });
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshBlockList();
        this.customUnblockKeyword = '';
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async unblockAllKeywords() {
      try {
        this.loading = true;
        const response = await this.unblockAllKeywordsAction(this.serverId);
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshBlockList();
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async allowPort() {
      if (!this.portToAllow) {
        this.$message.warning('');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.allowInboundPortsAction({
          serverId: this.serverId,
          ports: this.portToAllow
        });
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshInboundPorts();
        this.portToAllow = '';
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async disallowPort(port) {
      try {
        this.loading = true;
        const response = await this.disallowInboundPortsAction({
          serverId: this.serverId,
          ports: port
        });
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshInboundPorts();
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async allowIP() {
      if (!this.ipToAllow) {
        this.$message.warning('IP');
        return;
      }
      
      try {
        this.loading = true;
        const response = await this.allowInboundIPsAction({
          serverId: this.serverId,
          ips: this.ipToAllow
        });
        this.commandOutput = response.data;
        this.$message.success('IP');
        this.refreshInboundIPs();
        this.ipToAllow = '';
      } catch (error) {
        this.$message.error('IP: ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async disallowIP(ip) {
      try {
        this.loading = true;
        const response = await this.disallowInboundIPsAction({
          serverId: this.serverId,
          ips: ip
        });
        this.commandOutput = response.data;
        this.$message.success('IP');
        this.refreshInboundIPs();
      } catch (error) {
        this.$message.error('IP: ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    confirmClearRules() {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.clearAllRules();
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });          
      });
    },
    async clearAllRules() {
      try {
        this.loading = true;
        const response = await this.clearAllRulesAction(this.serverId);
        this.commandOutput = response.data;
        this.$message.success('');
        this.refreshBlockList();
        this.refreshInboundPorts();
        this.refreshInboundIPs();
        this.refreshSSHPort();
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>

<style scoped>
.rules-container {
  padding: 20px;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.server-info {
  margin-bottom: 20px;
  padding: 10px;
  background-color: #f5f7fa;
  border-radius: 4px;
}

.output {
  background-color: #f5f5f5;
  padding: 10px;
  border-radius: 4px;
  font-family: monospace;
  white-space: pre-wrap;
  word-break: break-all;
  max-height: 300px;
  overflow-y: auto;
}

.el-divider {
  margin: 15px 0;
}
</style> 