<template>
  <div class="servers-container">
    <div class="page-header">
      <h1></h1>
      <el-button type="primary" @click="showAddServerDialog"></el-button>
    </div>

    <el-table
      v-loading="loading"
      :data="servers"
      border
      style="width: 100%"
    >
      <el-table-column
        prop="name"
        label=""
        width="180"
      ></el-table-column>
      <el-table-column
        prop="host"
        label=""
        width="180"
      ></el-table-column>
      <el-table-column
        prop="port"
        label="SSH"
        width="100"
      ></el-table-column>
      <el-table-column
        prop="username"
        label=""
        width="120"
      ></el-table-column>
      <el-table-column
        prop="status"
        label=""
        width="100"
      >
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.status === 'online' ? 'success' : (scope.row.status === 'error' ? 'danger' : 'info')"
          >
            {{ statusText[scope.row.status] }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label=""
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.row)"
          ></el-button>
          <el-button
            v-if="scope.row.status !== 'online'"
            size="mini"
            type="success"
            @click="handleConnect(scope.row)"
          ></el-button>
          <el-button
            v-else
            size="mini"
            type="warning"
            @click="handleDisconnect(scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="primary"
            @click="handleManageRules(scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- / -->
    <el-dialog
      :title="isEdit ? '' : ''"
      :visible.sync="dialogVisible"
      width="50%"
    >
      <server-form
        :is-edit="isEdit"
        :server-data="currentServer"
        @submit="handleFormSubmit"
      ></server-form>
    </el-dialog>
  </div>
</template>

<script>
import ServerForm from '@/components/ServerForm.vue';
import { mapActions } from 'vuex';

export default {
  name: 'ServersView',
  components: {
    ServerForm
  },
  data() {
    return {
      loading: false,
      servers: [],
      dialogVisible: false,
      isEdit: false,
      currentServer: null,
      statusText: {
        'online': '',
        'offline': '',
        'error': ''
      }
    };
  },
  created() {
    this.fetchServers();
  },
  methods: {
    ...mapActions('servers', [
      'getAllServers',
      'createServer',
      'updateServer',
      'deleteServer',
      'connectServer',
      'disconnectServer'
    ]),
    async fetchServers() {
      this.loading = true;
      try {
        const response = await this.getAllServers();
        this.servers = response.data;
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    showAddServerDialog() {
      this.isEdit = false;
      this.currentServer = null;
      this.dialogVisible = true;
    },
    handleEdit(server) {
      this.isEdit = true;
      this.currentServer = { ...server };
      this.dialogVisible = true;
    },
    async handleFormSubmit(formData) {
      try {
        if (this.isEdit) {
          await this.updateServer({
            id: this.currentServer._id,
            data: formData
          });
          this.$message.success('');
        } else {
          await this.createServer(formData);
          this.$message.success('');
        }
        this.dialogVisible = false;
        this.fetchServers();
      } catch (error) {
        this.$message.error(error.message);
      }
    },
    async handleDelete(server) {
      try {
        await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        });
        
        await this.deleteServer(server._id);
        this.$message.success('');
        this.fetchServers();
      } catch (error) {
        if (error !== 'cancel') {
          this.$message.error(': ' + error.message);
        }
      }
    },
    async handleConnect(server) {
      try {
        this.loading = true;
        await this.connectServer(server._id);
        this.$message.success('');
        this.fetchServers();
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    async handleDisconnect(server) {
      try {
        this.loading = true;
        await this.disconnectServer(server._id);
        this.$message.success('');
        this.fetchServers();
      } catch (error) {
        this.$message.error(': ' + error.message);
      } finally {
        this.loading = false;
      }
    },
    handleManageRules(server) {
      this.$router.push({ name: 'rules', params: { serverId: server._id } });
    }
  }
};
</script>

<style scoped>
.servers-container {
  padding: 20px;
}
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
</style> 